___CSS_LOADER_EXPORT___.push([module.id, `.simple-fullscreen > div {
    position: inherit !important;
  }

.simple-fullscreen{
    width: 100%;
    height: 100%;
}

.fullscreen-button {
    position: absolute;
    z-index: 100;
    bottom:  5px;
    right:  5px;
    background: rgba(0,0,0,0.05);
    border:  0;
    width:  40px;
    height:  40px;
    border-radius: 50%;
    box-sizing: border-box;
    transition:  transform .3s;
    font-size: 0;
    opacity: 1;
    pointer-events: auto;
    cursor:  pointer;
  }
  .fullscreen-button:hover {
    transform: scale(1.125);
  }
  .fullscreen-button span {
    background: white;
    width:  4px;
    height:  4px;
    border-top:  2.5px solid #111; /* color */
    border-left:  2.5px solid #111; /* color */
    position: absolute;
    outline: 1px solid transparent;
    -webkit-backface-visibility: hidden;
    transform: translateZ(0);
    will-change: transform;
    -webkit-perspective: 1000;
    transition:  .3s;
    transition-delay: .75s;
  }
  .fullscreen-button span:nth-child(1) {
    top: 11px;
    left: 11px;
  }
  .fullscreen-button span:nth-child(2) {
    top: 11px;
    left: 22px;
    transform: rotate(90deg);
  }
  .fullscreen-button span:nth-child(3) {
    top: 22px;
    left: 11px;
    transform: rotate(-90deg);
  }
  .fullscreen-button span:nth-child(4) {
    top: 22px;
    left: 22px;
    transform: rotate(-180deg);
  }
  
  /* Fullscreen True
  ------------------------------*/
  [fullscreen] .fullscreen-button span:nth-child(1) {
    top: 22px;
    left: 22px;
  }
  [fullscreen] .fullscreen-button span:nth-child(2) {
    top: 22px;
    left: 11px;
  }
  [fullscreen] .fullscreen-button span:nth-child(3) {
    top: 11px;
    left: 22px;
  }
  [fullscreen] .fullscreen-button span:nth-child(4) {
    top: 11px;
    left: 11px;
  }
  
  /* Dark Style
  ------------------------------*/
  [light-mode=dark] {
    background: #111;
    color:  #fff;
  }
  [light-mode=dark] .fullscreen-button {
    background: rgba(255,255,255,.05);
  }
  
  [light-mode=dark] .fullscreen-button span {
    border-top:  2.5px solid #fff;
    border-left:  2.5px solid #fff;
  }`, "",{"version":3,"sources":["webpack://./src/lib/components/style.css"],"names":[],"mappings":"AAAA;IACI,4BAA4B;EAC9B;;AAEF;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,WAAW;IACX,4BAA4B;IAC5B,UAAU;IACV,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,sBAAsB;IACtB,0BAA0B;IAC1B,YAAY;IACZ,UAAU;IACV,oBAAoB;IACpB,gBAAgB;EAClB;EACA;IACE,uBAAuB;EACzB;EACA;IACE,iBAAiB;IACjB,WAAW;IACX,YAAY;IACZ,6BAA6B,EAAE,UAAU;IACzC,8BAA8B,EAAE,UAAU;IAC1C,kBAAkB;IAClB,8BAA8B;IAC9B,mCAAmC;IACnC,wBAAwB;IACxB,sBAAsB;IACtB,yBAAyB;IACzB,gBAAgB;IAChB,sBAAsB;EACxB;EACA;IACE,SAAS;IACT,UAAU;EACZ;EACA;IACE,SAAS;IACT,UAAU;IACV,wBAAwB;EAC1B;EACA;IACE,SAAS;IACT,UAAU;IACV,yBAAyB;EAC3B;EACA;IACE,SAAS;IACT,UAAU;IACV,0BAA0B;EAC5B;;EAEA;iCAC+B;EAC/B;IACE,SAAS;IACT,UAAU;EACZ;EACA;IACE,SAAS;IACT,UAAU;EACZ;EACA;IACE,SAAS;IACT,UAAU;EACZ;EACA;IACE,SAAS;IACT,UAAU;EACZ;;EAEA;iCAC+B;EAC/B;IACE,gBAAgB;IAChB,YAAY;EACd;EACA;IACE,iCAAiC;EACnC;;EAEA;IACE,6BAA6B;IAC7B,8BAA8B;EAChC","sourcesContent":[".simple-fullscreen > div {\n    position: inherit !important;\n  }\n\n.simple-fullscreen{\n    width: 100%;\n    height: 100%;\n}\n\n.fullscreen-button {\n    position: absolute;\n    z-index: 100;\n    bottom:  5px;\n    right:  5px;\n    background: rgba(0,0,0,0.05);\n    border:  0;\n    width:  40px;\n    height:  40px;\n    border-radius: 50%;\n    box-sizing: border-box;\n    transition:  transform .3s;\n    font-size: 0;\n    opacity: 1;\n    pointer-events: auto;\n    cursor:  pointer;\n  }\n  .fullscreen-button:hover {\n    transform: scale(1.125);\n  }\n  .fullscreen-button span {\n    background: white;\n    width:  4px;\n    height:  4px;\n    border-top:  2.5px solid #111; /* color */\n    border-left:  2.5px solid #111; /* color */\n    position: absolute;\n    outline: 1px solid transparent;\n    -webkit-backface-visibility: hidden;\n    transform: translateZ(0);\n    will-change: transform;\n    -webkit-perspective: 1000;\n    transition:  .3s;\n    transition-delay: .75s;\n  }\n  .fullscreen-but